body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav code a {
  color: white;
  margin-left: 15px;
  text-decoration: none;
}

.dark {
  background-color: black !important;
  color: white !important;
  border-color: white !important;
}

.light {
  background-color: white !important;
  color: black !important;
  border-color: black !important;
}

img {
  width: 50px;
}

nav {
  display: flex;
  align-items: center;
  justify-content: center;
}


.hero {
  height: 60vh;
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero div {
  display: flex;
  flex-direction: column;
  align-items: start;
  max-width: 700px;
}

.hero div h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 5px;
  font-size: xxx-large;
}

.hero div p {
  margin: 5px;
}

.hero img {
  width: 200px;
  height: 200px;
  margin-right: 50px;
}

.textForm {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}

.textForm .div-main {
  display: flex;
  margin: 50px;
  width: 80%;
  max-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.textForm div textarea {
  margin-bottom: 10px;
  width: 100%;
  border: none;
  padding: 3px;
}

.textForm .flex {
  display: flex;
  width: 100%;
}

.textForm .flex p {
  width: 100%;
  text-align: right;
}

.textForm textarea:focus {
  border: none;
  outline: none;
}

.textForm div h3 {
  font-size: x-large;
}

.textForm div button {
  font-size: medium;
  margin: 5px;
  padding: 8px;
  border-radius: 5px;
  border-color: aqua;
  background-color: aqua;
  cursor: pointer;
}

.alert {
  position: fixed;
  right: 20px;
  top: 70px;
  display: flex;
  align-items: center;
  justify-content: right;
  background: rgb(0, 242, 117);
  width: fit-content;
  border-radius: 5px;
}

.alert p {
  margin: 8px;
  margin-left: 20px;
  margin-right: 20px;
}

.hide {
  display: none;
}

.visible {
  display: flex;
}

footer {
  background-color: black;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

ul {
  display: flex;
  flex-direction: column;
}

.logo-ul-footer {
  align-items: center;
}

ul img {
  width: 100px;
  margin: 10px;
}

ul p {
  color: white;
}

li {
  list-style: none;
  padding: 5px;
  color: white;
}